	import { sendAjax } from '@/common/js/sendAjax.js';
	import config from '@/apiConfig';
	const {getCleanerDeail,getCleanerWork} = config.api;
export default {
	data() {
		return {
			id:null,
			basicsList: [{name: '预约时间'},{name: '完善信息'},{name: '估价'}],
			// 0 预约时间 1 预约时间+完善信息 2  预约时间+完善信息+提交
			scroll: 0,
			detail:'',
			chooseType:'',
			chooseData:'',
			dataList: []
		};
	},
	methods: {
		onLoad: function(e) {
			console.log(e)
			this.id = e.id;
			this.getCleanerDeail();
			this.getCleanerWork();
		},
		getCleanerDeail(){
			var that = this;
			let infoOpt = {
				url: getCleanerDeail,
				type: 'POST',
				data: {
					id:this.id
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				that.detail = res;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		getCleanerWork(){
			var that = this;
			let infoOpt = {
				url: getCleanerWork,
				type: 'POST',
				data: {
					cleanerId:this.id
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				var workList = res;
				for(var i in workList){
					workList[i].workDate = workList[i].workDate.substring(5,10)
				}
				that.dataList = workList;
				console.log(workList)
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		//点击时间预约
		yuyue(e){
			console.log(e)
			var type = e.currentTarget.dataset.type;
			var data = e.currentTarget.dataset.data;
			this.chooseType = type;
			this.chooseData = data;
		},
		//拨打电话
		phone(e){
			console.log(e)
			uni.makePhoneCall({
			  phoneNumber: e.currentTarget.dataset.phone
			})
		},
		//下一页
		toSub() {
			var id = this.id;
			var day = this.chooseData;
			var type = this.chooseType;
			
			uni.navigateTo({
				url: '../subscribeSub/subscribeSub?day=' + day +'&type=' + type +'&id='+id
			});
		}
	}
};
